.choose-model {
    padding: 0 1rem;
    width: 100%;
    background-color: #fafafa;
}

.choose-model__content-wrap {
    margin: 0 auto;
    padding: 100px 0;
    max-width: 1140px;
    width: 100%;
    @media screen and (--tablet) {
        padding: 70px 0;
    }
    @media screen and (--tablet) {
        padding: 50px 0;
    }
}

.choose-model__title {
    margin-bottom: 70px;
    color: #000000;
    text-align: center;
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;
    @media screen and (--tablet) {
        font-size: 32px;
        line-height: 40px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.choose-model__nav {
    display: flex;
    justify-content: center;
    margin-bottom: 35px;
    width: 100%;
}

.choose-model__nav-button {
    margin: 0 10px 20px;
    padding: 0 20px;
    border: 1px solid #f2f2f2;
    border-radius: 20px;
    background-color: #ffffff;
    color: #808080;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 16px;
    font-family: OpenSansBold;
    line-height: 38px;
    transition-duration: var(--transitionDuration);
    cursor: pointer;
    &:hover {
        background-color: #f2f2f2;
    }
    &.active {
        border-color: #ff4b7b;
        background-color: #ff4b7b;
        color: #fff;
    }
}

.choose-model__item {
    width: 100%;
}

.choose-model__item-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}



.choose-model__description {
    width: calc(100% - 360px - 30px);
}

.choose-model__description-title {
    color: #141414;
    font-size: 30px;
    font-weight: 400;
    line-height: 32px;
    background-position: left top;
    background-repeat: no-repeat;
    background-image: url(../images/choose-model-1.png);
    padding-left: 39px;
    margin-bottom: 30px;
}

.choose-model__description-nav {
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
}

.choose-model__description-nav-item {
    transition-duration: var(--transitionDuration);
    cursor: pointer;
    line-height: 52px;
    color: #bfbfbf;
    font-family: OpenSansBold;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    &:not(:last-child) {
        padding-right: 32px;
    }
    &.active {
        color: #141414;
    }
    &:hover {
        color: #141414;
    }
}

.choose-model__description-tabs-wrap {
    width: 100%;
}

.choose-model__description-tab {
    width: 100%;
}

.choose-model__description-caption {
    width: 100%;
    padding-top: 30px;
}

.choose-model__advantages {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.choose-model__advantages-item {
    width: 25%;
    padding: 42px 10px 0;
    background-position: top center;
    background-repeat: no-repeat;
    background-image: url(../images/choose-model-2.png);
    color: #141414;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    margin-bottom: 38px;
    text-align: center;
    &:not(:last-child) {
        border-right: 1px solid #e6e6e6;
    }
}

.choose-model__advantages-text {
    color: #141414;
}