.work {
    padding: 0 1rem;
    width: 100%;
    background-color: #fff;
    margin-bottom: -80px;
}

.work__content-wrap {
    margin: 0 auto;
    padding: 100px 0;
    max-width: 1140px;
    width: 100%;
    @media screen and (--tablet) {
        padding: 70px 0;
    }
    @media screen and (--tablet) {
        padding: 50px 0;
    }
}

.work__title {
    margin-bottom: 79px;
    color: #000000;
    text-align: center;
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;
    @media screen and (--tablet) {
        font-size: 32px;
        line-height: 40px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.work__conteiner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}

.work__list {
    width: calc(50% - 1rem);
    list-style: none;
    counter-reset: workItemTitle;
}

.work__list-item {
    position: relative;
    padding-left: 130px;
    min-height: 74px;
    font-size: 14px;
    line-height: 20px;
    &:not(:last-child) {
        margin-bottom: 50px;
    }
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        color: #e6e6e6;
        content: '0' counter(workItemTitle);
        counter-increment: workItemTitle;
        font-weight: 700;
        font-size: 100px;
        font-family: OpenSansBold;
        line-height: 72px;
    }
}

.work__list-item-title {
    color: #000000;
    text-transform: uppercase;
    font-weight: 700;
    font-family: OpenSansBold;
}

.work__list-item-text {
    color: #a8a8a8;
    font-weight: 400;
}

.work__order {
    float: right;
    margin-left: 39px;
    padding: 0 21px;
    border: 2px solid transparent;
    border-radius: 20px;
    background-color: #ff4b7b;
    color: #fff;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;
    font-family: OpenSansBold;
    line-height: 36px;
    &:hover {
        border-color: currentColor;
        background-color: transparent;
        color: #ff4b7b;
    }
}

.work__accent {
    color: #ff4b7b;
    text-transform: uppercase;
    font-weight: 700;
    font-family: OpenSansBold;
}

.work__image-wrap {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    width: calc(50% - 1rem);
}

.work__image {
    max-width: 100%;
    max-height: 100%;
    transform: translateY(120px);
}