.gift {
    padding: 0 1rem;
    width: 100%;
    background-image: url(../images/gift-1.png);
    background-position: center;
    background-size: cover;
}

.gift__content-wrap {
    margin: 0 auto;
    padding: 100px 0;
    max-width: 1140px;
    width: 100%;
    @media screen and (--tablet) {
        padding: 70px 0;
    }
    @media screen and (--tablet) {
        padding: 50px 0;
    }
}

.gift__title {
    margin-bottom: 67px;
    color: #ffffff;
    text-align: center;
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;
    @media screen and (--tablet) {
        font-size: 32px;
        line-height: 40px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.gift__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}

.gift__content {
    width: calc(50% - 1rem);
}

.gift__content-title {
    margin-bottom: 30px;
    max-width: 445px;
    color: #ffffff;
    font-size: 24px;
    line-height: 36px;
}

.gift__content-subtitle {
    margin-bottom: 59px;
    color: #ffffff;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
}

.gift__content-list {
    list-style: none;
}

.gift__content-list-item {
    padding-left: 20px;
    background-image: url(../images/gift-2.png);
    background-position: left 5px;
    background-repeat: no-repeat;
    color: #ffffff;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    &:not(:last-child) {
        margin-bottom: 20px;
    }
}

.gift__slider {
    position: relative;
    width: calc(50% - 1rem);
}

.gift__slider-items-wrap {
    perspective: 12000px;
}

.gift__slider-item {
    padding: 38px 98px;
    background-position: center;
    background-origin: content-box;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 10px;
    background-color: #ffffff;
    box-shadow: 0 5px 30px rgba(35, 31, 32, 0.15);
}

.gift__slider-button {
    width: 12px;
    height: 22px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 100;
    &--next {
        right: 30px;
        background-image: url(../images/gift-5.png);
    }
    &--prev {
        left: 30px;
        background-image: url(../images/gift-4.png);
    }
}