.form-order {
    width: 100%;
}

.form-order__title {
    padding: 25px 48px;
    border-radius: 10px 10px 0 0;
    background-image: linear-gradient(to right, #ff4b7b 0%, #ffd133 100%);
    color: #ffffff;
    text-align: center;
    font-weight: 400;
    font-size: 30px;
}

.form-order__fieldset {
    padding: 50px 48px;
    border-radius: 0 0 10px 10px;
    background-color: #fff;
}

.form-order__field {
    margin-bottom: 10px;
    width: 100%;
    height: 42px;
    border: 1px solid #dfdfdf;
    border-radius: 21px;
    color: #a8a8a8;
    text-align: center;
    font-weight: 400;
    font-size: 14px;
    &[data-form-field-invalid] {
        border-color: #ff4b7b;
    }
    &:last-of-type {
        margin-bottom: 20px;
    }
}

.form-order__status {
    display: none;
    padding-bottom: 10px;
    color: #ff4b7b;
    text-align: center;
    font-size: 12px;
}

.form-order__send-button {
    padding: 12px 16px;
    width: 100%;
    border: 2px solid transparent;
    border-radius: 30px;
    background-color: #ff4b7b;
    color: #ffffff;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 24px;
    &:hover {
        border-color: currentColor;
        background-color: transparent;
        color: #ff4b7b;
    }
}