.lightbox {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9998;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: auto;
    padding: 2rem;
    width: 100%;
    height: 100%;
    background-color: #4d4d4d;
    transition-duration: var(--transitionDuration);
    &--animation {
        &--enter {
            opacity: 0;
        }
        &--leave-active {
            opacity: 0;
        }
    }
}

.lightbox__container {
    @nest .lightbox--animation--enter & {
        transform: scale(1.1);
    }
    @nest .lightbox--animation--leave-active & {
        transform: scale(.9);
    }
    position: relative;
    padding: 0;
    max-width: 456px;
    max-height: calc(100vh - 4rem);
    border-radius: 10px;
    background-color: #ffffff;
    transition-duration: var(--transitionDuration);
}

.lightbox__close {
    position: absolute;
    top: -24px;
    right: -24px;
    width: 24px !important;
    height: 24px !important;
    border-radius: 50%;
    background-image: url(../images/lightbox-close.svg);
    background-position: left top;
    background-size: contain;
    background-repeat: no-repeat;
    &:hover {
        background-image: url(../images/lightbox-close-a.svg);
    }
}