.promo {
    position: relative;
    padding: 0 1rem;
    width: 100%;
    background-image: url(../images/promo__bgd-1.png);
    background-position: center bottom;
    background-size: auto 100%;
    background-repeat: no-repeat;
}

.promo__content-wrap {
    position: relative;
    z-index: 1;
    margin: 0 auto;
    padding-bottom: 238px;
    max-width: 1140px;
}

.promo__top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 28px;
    padding: 24px 0;
    width: 100%;
}

.promo__logo {
    display: block;
    width: 237px;
    height: 60px;
    background-image: url(../images/promo__logo.png);
    background-position: left center;
    background-size: contain;
    background-repeat: no-repeat;
}

.promo__address {
    color: #ffffff;
    text-align: right;
}

.promo__phone {
    display: block;
    color: #ffffff;
    font-weight: 700;
    font-size: 30px;
    line-height: 40px;
    &:hover {
        color: #ff4b7b;
    }
}

.promo__phone-description {
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
}

.promo__center {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.promo__info {}

.promo__info-title {
    color: #ffffff;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 60px;
}

.promo__info-subtitle {
    margin-bottom: 38px;
    color: #ffffff;
    font-weight: 700;
    font-size: 35px;
    line-height: 51px;
}

.promo__info-text {
    margin-bottom: 53px;
    color: #ffffff;
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
}

.promo__info-list {
    margin-bottom: 50px;
    list-style: none;
}

.promo__info-list-item {
    padding-left: 34px;
    background-image: url(../images/promo__li.png);
    background-position: left top;
    background-repeat: no-repeat;
    color: #ffffff;
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    &:not(:last-child) {
        margin-bottom: 18px;
    }
}

.promo__info-order {
    padding: 20px 26px;
    border: 2px solid transparent;
    border-radius: 36px;
    background-color: #ffffff;
    color: #ff4b7b;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    &:hover {
        border-color: currentColor;
        background-color: transparent;
        color: #fff;
    }
}

.promo__watches {
    position: relative;
    align-self: flex-start;
    margin-right: 52px;
    width: 467px;
    height: 475px;
}

.promo__watches-item {
    &:hover:not(.promo__watches-item--state--active) {
        transform: scale(1.2);
        cursor: pointer;
    }
    position: absolute;
    width: 125px;
    height: 125px;
    transition-duration: var(--transitionDuration);
    &--1 {
        top: 97%;
        left: 30%;
    }
    &--2 {
        top: 74%;
        left: -2%;
    }
    &--3 {
        top: 34%;
        left: 0%;
    }
    &--4 {
        top: 3%;
        left: 18%;
    }
    &--5 {
        top: -5%;
        left: 49%;
    }
    &--6 {
        top: 0%;
        left: 80%;
    }
    &--state--active {
        top: 22%;
        left: 23%;
        z-index: 1;
        width: 360px;
        height: 388px;
    }
}

.promo__watches-item-image {
    max-width: 100%;
    max-height: 100%;
}

.promo__watches-label {
    @nest .promo__watches-item--state--active & {
        top: 14% !important;
        left: 52% !important;
        width: 91px;
        height: 91px;
        font-size: 29px;
        line-height: 36px;
    }
    @nest .promo__watches-item--1 & {
        top: 52%;
        left: 52%;
    }
    @nest .promo__watches-item--2 & {
        top: 68%;
        left: 14%;
    }
    @nest .promo__watches-item--3 & {
        top: -16%;
        left: 36%;
    }
    @nest .promo__watches-item--4 & {
        top: -18%;
        left: 27%;
    }
    @nest .promo__watches-item--5 & {
        top: 13%;
        left: 69%;
    }
    @nest .promo__watches-item--6 & {
        top: 44%;
        left: 51%;
    }
    position: absolute;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border: 1px solid #ffffff;
    border-radius: 50%;
    background-color: #ff4b7b;
    color: #ffffff;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 13px;
    font-family: OpenSansBold;
    line-height: 20px;
    transition-duration: var(--transitionDuration);
}

.promo__bubble {
    position: absolute;
    border-radius: 50%;
    background-image: linear-gradient(to right, #ff4b7b 0%, #ffd133 100%);
    box-shadow: 0 5px 30px rgba(35, 31, 32, 0.2);
    transform: translate(0, 0) scale(1);
    animation: promoBubble 7s ease infinite;
    &--1 {
        top: -66px;
        left: -68px;
        width: 120px;
        height: 120px;
        animation-delay: .3s;
    }
    &--2 {
        top: 24%;
        left: 4%;
        width: 20px;
        height: 20px;
        animation-delay: .6s;
    }
    &--3 {
        top: 97%;
        left: 2%;
        width: 45px;
        height: 45px;
        animation-delay: .9s;
    }
    &--4 {
        top: 52%;
        left: 41%;
        width: 65px;
        height: 65px;
        animation-delay: 1.2s;
    }
    &--5 {
        top: 3%;
        right: 37%;
        width: 25px;
        height: 25px;
        animation-delay: 1.5s;
    }
    &--6 {
        top: 14%;
        right: 19%;
        width: 50px;
        height: 50px;
        animation-delay: 1.8s;
    }
    &--7 {
        top: 68%;
        right: 10%;
        width: 45px;
        height: 45px;
        animation-delay: 2.1s;
    }
}

@keyframes promoBubble {
    0% {
        transform: translate(0, 0) scale(1);
    }
    50% {
        transform: translate(0, -32px) scale(.8);
    }
    100% {
        transform: translate(0, 0) scale(1);
    }
}