.models {
    padding: 0 1rem;
    width: 100%;
    background-color: #fff;
}

.models__content-wrap {
    margin: 0 auto;
    padding: 0 0 100px;
    max-width: 1140px;
    width: 100%;
    @media screen and (--tablet) {
        padding: 0 0 70px;
    }
}

.models__title {
    margin-bottom: 85px;
    color: #333333;
    text-align: center;
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;
    @media screen and (--tablet) {
        font-size: 32px;
        line-height: 40px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.models__items-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    align-items: flex-start;
}

.models__item {
    position: relative;
    padding: 35px 14px calc(32px + 45px);
    width: calc((100% - 30px*2)/3);
    border: 1px solid #e6e6e6;
    border-radius: 10px;
    background-color: #ffffff;
    transition-duration: var(--transitionDuration);
    &:not(:last-child) {
        margin-bottom: 32px;
    }
    &:hover {
        box-shadow: 0 5px 30px rgba(35, 31, 32, 0.15);
    }
}

.models__item-label {
    position: absolute;
    top: 0;
    left: 50%;
    padding: 0 .5rem;
    min-width: 60px;
    border-radius: 15px;
    background-color: #ff4b7b;
    box-shadow: 0 5px 30px rgba(35, 31, 32, 0.15);
    color: #ffffff;
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;
    font-family: OpenSansBold;
    line-height: 30px;
    transform: translate(-50%, -50%);
}

.models__item-title {
    margin-bottom: 20px;
    color: #333333;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 18px;
    font-family: OpenSansBold;
    text-align: center;
}

.models__item__tabs-wrap {
    width: 100%;
    margin-bottom: 20px;
}

.models__item-tab {
    height: 175px;
    width: 100%;
}

.models__item-image-wrap {
    position: relative;
    width: 100%;
    height: 175px;
    overflow: hidden;
}

.models__item-image {
    max-width: 100%;
    max-height: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.models__item-cost {
    display: block;
    margin-bottom: 18px;
    color: #39ac29;
    text-align: center;
    font-weight: 700;
    font-size: 30px;
    font-family: OpenSansBold;
    line-height: 30px;
}

.models__item-cost-currency {
    font-size: 18px;
}

.models__item-colors-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 18px;
    list-style: none;
}

.models__item-color {
    margin: 0 5px;
    width: 24px;
    height: 24px;
    border: 2px solid #fff;
    border-radius: 50%;
    cursor: pointer;
    &:hover {
        box-shadow: 0 3px 9px rgba(0, 0, 0, 0.1);
    }
    &.active {
        box-shadow: 0 3px 9px rgba(0, 0, 0, 0.1);
    }
    &--black {
        background-color: black;
    }
    &--blue {
        background-color: #7fd9ee;
    }
    &--cyan {
        background-color: cyan;
    }
    &--gold {
        background-color: gold;
    }
    &--green {
        background-color: #aed66a;
    }
    &--orange {
        background-color: orange;
    }
    &--pink {
        background-color: #f28aaa;
    }
    &--silver {
        background-color: silver;
    }
    &--white {
        background-color: white;
    }
    &--yellow {
        background-color: yellow;
    }
}

.models__item-caption {}

.models__item-param {
    padding-left: 18px;
    position: relative;
    background-position: left top;
    background-repeat: no-repeat;
    color: #000000;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    margin-bottom: 1em;
    &--1 {
        background-image: url(../images/models-i1.png);
        background-position: 3px 3px;
    }
    &--2 {
        background-image: url(../images/models-i2.png);
        background-position: 3px 4px;
    }
    &--3 {
        background-image: url(../images/models-i3.png);
        background-position: 4px 2px;
    }
    &--4 {
        background-image: url(../images/models-i4.png);
        background-position: 1px 1px;
    }
}

.models__item-order {
    position: absolute;
    bottom: 32px;
    left: 14px;
    width: calc(100% - 28px);
    color: #ffffff;
    font-family: OpenSansBold;
    font-size: 16px;
    font-weight: 700;
    line-height: 30px;
    text-transform: uppercase;
    background-color: #ff4b7b;
    border-radius: 22px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid transparent;
    &:hover {
        color: #ff4b7b;
        border-color: currentColor;
        background-color: transparent;
    }
}

.models__slider {
    width: 100%;
    padding-top: 15px;
}

.models__slider-items-wrap {}