.sertificates {
    padding: 0 1rem;
    width: 100%;
    background-color: #fafafa;
}

.sertificates__content-wrap {
    position: relative;
    margin: 0 auto;
    padding: 100px 0;
    max-width: 1140px;
    width: 100%;
    @media screen and (--tablet) {
        padding: 70px 0;
    }
    @media screen and (--tablet) {
        padding: 50px 0;
    }
}

.sertificates__title {
    margin-bottom: 17px;
    color: #000000;
    text-align: center;
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;
    @media screen and (--tablet) {
        font-size: 32px;
        margin-bottom: 15px;
        line-height: 40px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.sertificates__subtitle {
    margin-bottom: 60px;
    color: #000000;
    text-align: center;
    font-weight: 400;
    font-size: 20px;
    line-height: 36px;
    @media screen and (--tablet) {
        margin-bottom: 38px;
    }
    @media screen and (--mobile) {
        font-size: 14px;
        line-height: 22px;
    }
}

.sertificates__slider {
    margin: 0 auto;
    width: 100%;
    max-width: 940px;
    @media screen and (--tablet) {
        max-width: calc(100% - 120px);
    }
}

.sertificates__slider-items-wrap {}

.sertificates__slider-item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 233px;
    cursor: pointer;
}

.sertificates__slider-item-image {
    max-width: 100%;
    max-height: 100%;
}

.sertificates__slider-button {
    position: absolute;
    bottom: 198px;
    width: 50px;
    height: 50px;
    border: 1px solid #f2f2f2;
    border-radius: 50%;
    background-color: #ffffff;
    background-position: center;
    background-repeat: no-repeat;
    &:hover {
        box-shadow: 0 5px 30px rgba(35, 31, 32, 0.15);
    }
    &--next {
        left: 0;
        background-image: url(../images/sertificates-1.png);
    }
    &--prev {
        right: 0;
        background-image: url(../images/sertificates-2.png);
    }
}