.select-color {
    width: 360px;
}

.select-color__watches {
    width: 100%;
}

.select-color__watches-tabs {
    height: 223px;
    margin-bottom: 32px;
}

.select-color__watches-images-wrap {
    position: relative;
    width: 100%;
    height: 223px;
}

.select-color__watches-image {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 100%;
    max-height: 100%;
    transform: translate(-50%, -50%);
}

.select-color__watches-colors-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-bottom: 32px;
    width: 100%;
    list-style: none;
}

.select-color__watches-color {
    margin: 0 5px;
    width: 24px;
    height: 24px;
    border: 2px solid #fff;
    border-radius: 50%;
    cursor: pointer;
    transition-duration: var(--transitionDuration);
    &:hover {
        box-shadow: 0 3px 9px rgba(0, 0, 0, 0.1);
    }
    &.active {
        box-shadow: 0 3px 9px rgba(0, 0, 0, 0.1);
    }
    &--black {
        background-color: black;
    }
    &--blue {
        background-color: #7fd9ee;
    }
    &--cyan {
        background-color: cyan;
    }
    &--gold {
        background-color: gold;
    }
    &--green {
        background-color: #aed66a;
    }
    &--orange {
        background-color: orange;
    }
    &--pink {
        background-color: #f28aaa;
    }
    &--silver {
        background-color: silver;
    }
    &--white {
        background-color: white;
    }
    &--yellow {
        background-color: yellow;
    }
}

.select-color__cost {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 20px;
    width: 100%;
}

.select-color__cost-item {
    width: 50%;
    &:first-child {
        padding-right: 1rem;
        border-right: 1px solid #e6e6e6;
    }
    &:last-child {
        padding-left: 1rem;
    }
}

.select-color__cost-title {
    color: #000000;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 12px;
}

.select-color__cost-summary {
    text-decoration: line-through;
    font-weight: 700;
    font-size: 30px;
    font-family: OpenSansBold;
    &--old {
        color: #bfbfbf;
    }
    &--new {
        color: #39ac29;
    }
}

.select-color__cost-summary-accent {
    font-weight: 700;
    font-size: 18px;
    font-family: OpenSansBold;
}

.select-color__order {
    margin-bottom: 20px;
    padding: 0 2rem;
    width: 100%;
    height: 56px;
    border: 2px solid transparent;
    border-radius: 30px;
    background-color: #ff4b7b;
    color: #ffffff;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 24px;
    font-family: OpenSansBold;
    &:hover {
        border-color: currentColor;
        background-color: transparent;
        color: #ff4b7b;
    }
}

.select-color__list {
    width: 100%;
    list-style: none;
}

.select-color__list-item {
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    padding-left: 30px;
    border: 1px solid #f2f2f2;
    border-radius: 16px;
    background-color: #ffffff;
    line-height: 30px;
    &:not(:last-child) {
        margin-bottom: 10px;
    }
}

.select-color__list-item-info {
    color: #808080;
    font-weight: 400;
    font-size: 14px;
}

.select-color__list-item-count {
    color: #ff4b7b;
    font-weight: 700;
    font-size: 14px;
    font-family: OpenSansBold;
}

.select-color__list-item-cost {
    color: #39ac29;
    font-weight: 700;
    font-size: 18px;
    font-family: OpenSansBold;
}

.select-color__list-item-rouble {
    color: #39ac29;
    font-weight: 700;
    font-size: 14px;
    font-family: OpenSansBold;
}

.select-color__list-item-order {
    position: relative;
    padding: 0 30px;
    border-radius: 16px;
    background-color: #ff4b7b;
    color: #ffffff;
    font-weight: 700;
    font-size: 14px;
    font-family: OpenSansBold;
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        background-color: #ff4b7b;
        content: 'Заказать';
        text-transform: none;
        line-height: 30px;
        opacity: 0;
        transition-duration: var(--transitionDuration);
    }
    &:hover {
        &::before {
            opacity: 1;
        }
    }
}