.original {
    padding: 0 1rem;
    width: 100%;
    background-image: url(../images/original-2.png), url(../images/original-1.png);
    background-position: calc(50% + 480px) -84px, center;
    background-size: auto, cover;
    background-repeat: no-repeat, no-repeat;
    @media screen and (--tablet) {
        background-size: contain, cover;
        background-position: 50vw center, center;
    }
    @media screen and (--mobile) {
        background-position: 100vw center, center;
    }
}

.original__content-wrap {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    padding: 100px 0;
    max-width: 1140px;
    @media screen and (--tablet) {
        padding: 70px 0;
    }
    @media screen and (--tablet) {
        padding: 50px 0;
    }
}

.original__text {
    width: 530px;
    @media screen and (--tablet) {
        width: 50%;
    }
    @media screen and (--mobile) {
        width: 100%;
    }
}

.original__title {
    margin-bottom: 70px;
    color: #ffffff;
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;

    @media screen and (--tablet) {
        font-size: 32px;
        line-height: 40px;
        margin-bottom: 43px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.original__list {
    list-style: none;
}

.original__list-item {
    padding-left: 31px;
    background-image: url(../images/original-3.png);
    background-position: left top;
    background-repeat: no-repeat;
    color: #ffffff;
    font-weight: 600;
    font-size: 14px;
    font-family: OpenSansBold;
    line-height: 24px;
    &:not(:last-child) {
        margin-bottom: 27px;
    }
}