.advantages {
    width: 100%;
    background-color: #fff;
    padding: 0 1rem;
}

.advantages__content-wrap {
    width: 100%;
    margin: 0 auto;
    max-width: 1140px;
    padding: 100px 0;
    @media screen and (--tablet) {
        padding: 70px 0;
    }
    @media screen and (--tablet) {
        padding: 50px 0;
    }
}

.advantages__title {
    color: #000000;
    font-size: 36px;
    font-weight: 400;
    line-height: 54px;
    text-align: center;
    margin-bottom: 72px;
    @media screen and (--tablet) {
        font-size: 32px;
        line-height: 40px;
        margin-bottom: 35px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.advantages__lists-wrap {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.advantages__list {
    width: calc(50% - 1rem);
    list-style: none;
    &--good {}
    &--bad {}
    @media screen and (--mobile) {
        width: 100%;
    }
}

.advantages__list-title {
    color: #000000;
    font-family: OpenSansBold;
    font-size: 18px;
    font-weight: 700;
    line-height: 28px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 32px;
}

.advantages__list-item {
    min-height: 22px;
    @nest .advantages__list--bad & {
        background-image: url(../images/advantages-1.png);
    }
    @nest .advantages__list--good & {
        background-image: url(../images/advantages-2.png);
    }
    background-repeat: no-repeat;
    background-position: left top;
    padding-left: 33px;
    color: #000000;
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    &:not(:last-child) {
        margin-bottom: 20px;
    }
}

.advantages__tabs {
    width: 100%;
}

.advantages__tabs-nav {
    list-style: none;
    display: flex;
    justify-content: center;
    margin-bottom: 35px;
    width: 100%;
}

.advantages__tabs-nav-item {
    margin: 0 10px 20px;
    padding: 0 20px;
    border: 1px solid #f2f2f2;
    border-radius: 20px;
    background-color: #ffffff;
    color: #808080;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 16px;
    font-family: OpenSansBold;
    line-height: 38px;
    transition-duration: var(--transitionDuration);
    cursor: pointer;
    &:hover {
        background-color: #f2f2f2;
    }
    &.active {
        border-color: #ff4b7b;
        background-color: #ff4b7b;
        color: #fff;
    }
}

