.delivery {
    padding: 0 1rem;
    width: 100%;
    background-image: url(../images/delivery-bgd.png);
    background-position: center top;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    z-index: 1;
}

.delivery__content-wrap {
    margin: 0 auto;
    padding: 180px 0 0;
    max-width: 1140px;
    width: 100%;
    position: relative;
    z-index: 2;
}

.delivery__title {
    margin-bottom: 26px;
    color: #ffffff;
    text-align: center;
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;
    @media screen and (--tablet) {
        font-size: 32px;
        line-height: 40px;
        margin-bottom: 12px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.delivery__subtitle {
    color: #ffffff;
    font-size: 24px;
    font-weight: 400;
    line-height: 36px;
    margin-bottom: 70px;
    text-align: center;
    @media screen and (--tablet) {
        font-size: 20px;
        margin-bottom: 48px;
    }
}

.delivery__slider {
    width: 100%;
    padding-bottom: 40px;
}

.delivery__items-wrap {
    width: 100%;
    /* display: flex;
     justify-content: space-between;
    flex-wrap: wrap;*/
}

.delivery__item {
    padding-top: 12px;
    width: calc((100% - 60px)/3);
    background-image: linear-gradient(to right, #ff4b7b 0%, #ffd133 100%);
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0 5px 30px rgba(35, 31, 32, 0.15);
    @media screen and (--tablet) {
        width: calc((100% - 2rem)/3);
    }
}

.delivery__item-caption {
    background-position: center 30px;
    background-repeat: no-repeat;
    padding: 97px 28px 28px;
    text-align: center;
    background-color: #fff;
    &--1 {
        background-image: url(../images/delivery-1.png);
    }
    &--2 {
        background-image: url(../images/delivery-2.png);
    }
    &--3 {
        background-image: url(../images/delivery-3.png);
    }
    @media screen and (--tablet) {
        padding: 97px 10px 23px;
    }
}

.delivery__item-title {
    color: #000000;
    font-family: OpenSansBold;
    margin-bottom: 25px;
    font-size: 18px;
    font-weight: 700;
    line-height: 28px;
    @media screen and (--tablet) {
        margin-bottom: 20px;
        line-height: 22px;
    }
}

.delivery__item-subtitle {
    color: #000000;
    font-family: OpenSansBold;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    margin-bottom: 29px;
    @media screen and (--tablet) {
        margin-bottom: 20px;
    }
}

.delivery__item-list {
    list-style: none;
    display: inline-block;
    margin-bottom: 31px;
    @media screen and (--tablet) {
        margin-bottom: 20px;
    }
}

.delivery__item-param {
    padding-left: 16px;
    background-position: left 3px;
    background-repeat: no-repeat;
    color: #000000;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    &--1 {
        background-image: url(../images/delivery-4.png);
    }
    &--2 {
        background-image: url(../images/delivery-5.png);
    }
}

.delivery__item-text {
    color: #808080;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
}

.delivery__slider-pagination {
    width: 100%;
    text-align: center;
    & .swiper-pagination-bullet {
        border: 1px solid #fff;
        position: relative;
        margin-right: 5px;
        margin-left: 5px;
        width: 8px;
        height: 8px;
        background-color: transparent;
        background-color: transparent;
        opacity: 1;
        transition-duration: var(--transitionDuration);
    }
    & .swiper-pagination-bullet-active {
        background-color: #fff;
    }
}

.delivery__bubble {
    position: absolute;
    border-radius: 50%;
    background-image: linear-gradient(to right, #ff4b7b 0%, #ffd133 100%);
    box-shadow: 0 5px 30px rgba(35, 31, 32, 0.2);
    transform: translate(0, 0) scale(1);
    animation: deliveryBubble 7s ease infinite;
    &--1 {
        top: 65px;
        left: -46px;
        width: 175px;
        height: 175px;
        animation-delay: .3s;
    }
    &--2 {
        top: 58%;
        left: 4%;
        width: 21px;
        height: 21px;
        animation-delay: .6s;
    }
    &--3 {
        top: 72%;
        left: 7%;
        width: 64px;
        height: 64px;
        animation-delay: .9s;
    }
    &--4 {
        top: 2%;
        left: 68%;
        width: 77px;
        height: 77px;
        animation-delay: 1.2s;
    }
    &--5 {
        top: 14%;
        right: 18%;
        width: 21px;
        height: 21px;
        animation-delay: 1.5s;
    }
    &--6 {
        top: 56%;
        right: 11%;
        width: 21px;
        height: 21px;
        animation-delay: 1.8s;
    }
    &--7 {
        top: -3%;
        right: 5%;
        width: 79px;
        height: 79px;
        animation-delay: 2.1s;
    }
    &--8 {
        top: -7%;
        right: 1%;
        width: 72px;
        height: 72px;
        animation-delay: 2.4s;
    }
}

@keyframes deliveryBubble {
    0% {
        transform: translate(0, 0) scale(1);
    }
    50% {
        transform: translate(0, -32px) scale(.8);
    }
    100% {
        transform: translate(0, 0) scale(1);
    }
}