.complect {
    width: 100%;
    /*background-image: linear-gradient(to right, #ff4b7b 0%, #ffd133 100%);*/
    background: linear-gradient(-128deg, #ff4b7b, #ffd133, #ff4b7b, #ffd133);
    background-size: 200% 200%;
    padding: 0 1rem;
    animation: complect 10s ease infinite;
}

@keyframes complect {
    0% {
        background-position: 0% 81%
    }
    50% {
        background-position: 100% 20%
    }
    100% {
        background-position: 0% 81%
    }
}

.complect__content-wrap {
    width: 100%;
    margin: 0 auto;
    max-width: 1140px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.complect__image-wrap {
    width: 390px;
    text-align: left;
    position: relative;
    padding-right: 2rem;
    @media screen and (--tablet) {
        width: 310px;
    }
    @media screen and (--mobile) {
        display: none;
    }
}

.complect__image {
    max-width: 100;
    max-height: 100%;
    position: absolute;
    left: 0;
    bottom: -30px;
}

.complect__caption {
    width: calc(100% - 390px);
    padding: 60px 0 100px;
    @media screen and (--tablet) {
        width: calc(100% - 330px);
    }
    @media screen and (--mobile) {
        width: 100%;
        padding: 60px 0;
    }
}

.complect__title {
    color: #ffffff;
    font-size: 32px;
    margin-bottom: 80px;
    font-weight: 400;
    line-height: 54px;
    @media screen and (--tablet) {
        font-size: 32px;
        line-height: 40px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.complect__items-wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.complect__item {
    width: calc((100% - 6rem)/4);
    background-position: left top;
    background-repeat: no-repeat;
    padding-top: 60px;
    color: #ffffff;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    &--1 {
        background-image: url(../images/complect-2.png);
    }
    &--2 {
        background-image: url(../images/complect-3.png);
    }
    &--3 {
        background-image: url(../images/complect-4.png);
    }
    &--4 {
        background-image: url(../images/complect-5.png);
    }
    @media screen and (--tablet) {
        width: calc((100% - 30px)/4);
    }
    @media screen and (--mobile) {
        width: calc(50% - 1rem);
        margin-bottom: 30px;
    }
}