.site-footer {
    position: relative;
    padding: 0 1rem;
    width: 100%;
}

.site-footer__content-wrap {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    padding: 42px 0;
    max-width: 1140px;
    width: 100%;
}

.site-footer__logo {
    display: block;
    width: 237px;
    height: 60px;
    background-image: url(../images/promo__logo.png);
    background-position: left center;
    background-size: contain;
    background-repeat: no-repeat;
    @media screen and (--mobile) {
        width: 100%;
        background-position: center;
        margin-bottom: 20px;
    }
}

.site-footer__address {
    color: #ffffff;
    text-align: right;
    @media screen and (--mobile) {
        width: 100%;
        text-align: center;
    }
}

.site-footer__phone {
    display: block;
    color: #ffffff;
    font-weight: 700;
    font-size: 30px;
    line-height: 40px;
    &:hover {
        color: #ff4b7b;
    }
}

.site-footer__phone-description {
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
}