.need {
    width: 100%;
    background-color: #fff;
    padding: 0 1rem;
}

.need__content-wrap {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    padding: 100px 0;
    display: flex;
    flex-wrap: wrap;
    background-image: url(../images/need-1.png);
    background-position: left bottom;
    background-repeat: no-repeat;
    @media screen and (--tablet) {
        padding: 70px 0;
        background-image: none;
    }
    @media screen and (--tablet) {
        padding: 50px 0;
    }
}

.need__text {
    width: 50%;
    margin-left: auto;
    @media screen and (--tablet) {
        width: 100%;
    }
}

.need__title {
    color: #000000;
    font-size: 30px;
    font-weight: 400;
    line-height: 54px;
    margin-bottom: 69px;
    @media screen and (--tablet) {
        font-size: 32px;
        margin-bottom: 42px;
        line-height: 40px;
        text-align: center;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.need__list {
    list-style: none;
}

.need__item {
    color: #000000;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    padding-left: 26px;
    background-position: left 1.5px;
    background-repeat: no-repeat;
    background-image: url(../images/need-2.png);
    &:not(:last-child) {
        margin-bottom: 16px;
    }
}