.options {
    padding: 0 1rem;
    width: 100%;
    background-color: #fafafa;
}

.options__content-wrap {
    margin: 0 auto;
    padding: 100px 0 50px;
    max-width: 1140px;
    width: 100%;
    @media screen and (--tablet) {
        padding: 70px 0 50px;
    }
}

.options__title {
    margin-bottom: 84px;
    color: #000000;
    text-align: center;
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;
    @media screen and (--tablet) {
        font-size: 32px;
        line-height: 40px;
    }
    @media screen and (--mobile) {
        font-size: 24px;
        line-height: 36px;
    }
}

.options__items-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}

.options__item {
    position: relative;
    margin-bottom: 50px;
    padding-left: 100px;
    min-height: 80px;
    width: calc(50% - 1rem);
    @media screen and (--tablet) {
        padding: 0;
        height: 100px;
        &.swiper-slide-active {
            &::before {
                opacity: 1;
            }
        }
    }
    &--icon {
        &--1 {
            &::before {
                background-image: url(../images/options-1.svg);
            }
        }
        &--2 {
            &::before {
                background-image: url(../images/options-2.svg);
            }
        }
        &--3 {
            &::before {
                background-image: url(../images/options-3.svg);
            }
        }
        &--4 {
            &::before {
                background-image: url(../images/options-4.svg);
            }
        }
        &--5 {
            &::before {
                background-image: url(../images/options-5.svg);
            }
        }
        &--6 {
            &::before {
                background-image: url(../images/options-6.svg);
            }
        }
        &--7 {
            &::before {
                background-image: url(../images/options-7.svg);
            }
        }
        &--8 {
            &::before {
                background-image: url(../images/options-8.svg);
            }
        }
        &--9 {
            &::before {
                background-image: url(../images/options-9.svg);
            }
        }
        &--10 {
            &::before {
                background-image: url(../images/options-10.svg);
            }
        }
    }
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: #ffffff;
        background-position: center;
        background-repeat: no-repeat;
        transition-duration: var(--transitionDuration);
        box-shadow: 0 5px 30px rgba(35, 31, 32, 0.15);
        content: '';
        @media screen and (--tablet) {
            top: 10px;
            left: 50%;
            transform: translateX(-50%);
            opacity: .15;
        }
    }
}

.options__item-text-wrap {
    position: relative;
    width: 100%;
    transition-duration: var(--transitionDuration);
    @media screen and (--tablet) {
        @nest .swiper-slide-active & {
            opacity: 1;
        }
        position: absolute;
        top: 140px;
        width: 300%;
        text-align: center;
        opacity: 0;
        transform: translateX(calc(-100% / 3));
    }
}

.options__item-title {
    margin-bottom: 11px;
    color: #000000;
    font-weight: 700;
    font-size: 14px;
    font-family: OpenSansBold;
    line-height: 20px;
}

.options__item-caption {
    color: #000000;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
}

.options__slider {
    padding-bottom: 230px;
    width: 100%;
}

.options__slider-items-wrap {}

.options__slider-pagination {
    width: 100%;
    text-align: center;
    & .swiper-pagination-bullet {
        border: 1px solid #464646;
        position: relative;
        margin-right: 5px;
        margin-left: 5px;
        width: 8px;
        height: 8px;
        background-color: transparent;
        background-color: transparent;
        opacity: 1;
        transition-duration: var(--transitionDuration);
    }
    & .swiper-pagination-bullet-active {
        background-color: #464646;
    }
}